import React, { useState } from "react";
import classNames from "classnames/bind";
import isEmpty from "validator/lib/isEmpty";

import styles from "./Login.module.scss";

const cx = classNames.bind(styles);

function Login({ setShowLogin }) {
  const [name, setName] = useState("");
  const [pasword, setPassword] = useState("");
  const [submitMsg, setSubmitMsg] = useState({});

  const handleOnChangeUsername = (e) => {
    setName(e.target.value);
  };

  const handleOnChangePassword = (e) => {
    setPassword(e.target.value);
  };

  const validateAll = () => {
    const msg = {};

    if (isEmpty(name)) {
      msg.name = "Please enter your name or email!";
    }
    if (isEmpty(pasword)) {
      msg.pasword = "Please enter your password!";
    }

    setSubmitMsg(msg);
    if (Object.keys(submitMsg).length > 0) return false;
    return true;
  };

  const handleSubmitLogin = () => {
    const isValid = validateAll();
    if (!isValid) {
      return;
    }
  };

  const handleCloseLogin = () => {
    setShowLogin(false);
  };
  return (
    <div className={cx("wrapper")}>
      <div className={cx("login__header")}>
        <span>Login</span>
        <i class="fa-solid fa-xmark" onClick={handleCloseLogin}></i>
      </div>
      <div className={cx("login__body")}>
        <div className={cx("login__bodyForm")}>
          <div className={cx("login__bodyFromInputHidden")}>
            <i
              class="fa-solid fa-user"
              style={{ width: "30px", fontSize: "16px", color: "#4c4c4c" }}
            ></i>
            <div className={cx("login__bodyFromInputHiddenInput")}>
              Username or email
              <input
                className={cx("login__bodyInput")}
                onChange={handleOnChangeUsername}
                id="input_username"
              />
            </div>
          </div>

          <p className={cx("msgValid")}>{submitMsg.name}</p>

          <div className={cx("login__bodyFromInputHidden")}>
            <i
              class="fa-solid fa-lock"
              style={{ width: "30px", fontSize: "16px", color: "#4c4c4c" }}
            ></i>
            <div className={cx("login__bodyFromInputHiddenInput")}>
              Password
              <input
                className={cx("login__bodyInput")}
                onChange={handleOnChangePassword}
              />
            </div>
          </div>

          <p className={cx("msgValid")}>{submitMsg.pasword}</p>
          <div className={cx("login__bodyCheckBox")}>
            <input
              type="checkbox"
              id="checkbox"
              style={{ marginRight: "10px" }}
            />
            <label htmlFor="checkbox" style={{ cursor: "pointer" }}>
              Remember me
            </label>
          </div>
          <button className={cx("login__bodyBtn")} onClick={handleSubmitLogin}>
            Login
          </button>
          <p className={cx("login__bodyNote")}>Lost your password</p>
        </div>
      </div>
    </div>
  );
}

export default Login;
