import React from "react";
import classNames from "classnames/bind";

import styles from "./Cart.module.scss";

const cx = classNames.bind(styles);

function Cart({ setShowCart, setShowOverlay }) {
  const handleCloseCart = () => {
    setShowCart(false);
    setShowOverlay(false);
  };
  return (
    <div className={cx("wrapper")}>
      <div className={cx("cart__header")}>
        <div className={cx("cart__headerBtnClose")} onClick={handleCloseCart}>
          <i class="fa-solid fa-xmark" style={{ fontSize: "25px" }}></i>
        </div>
        <div className={cx("cart__headerTitle")}>
          <div>
            <i
              class="fa-solid fa-cart-shopping"
              style={{ fontSize: "30px", width: "50px" }}
            ></i>
            <span>Cart</span>
          </div>
        </div>
      </div>
      <div className={cx("cart__body")}>
        {/* <div className={cx("cart__headerBtnClose")} onClick={handleCloseCart}>
          <i class="fa-solid fa-cart-shopping" style={{ fontSize: "30px" }}></i>
        </div> */}
      </div>
      <div className={cx("cart__footer")}>
        <div className={cx("cart__footerSubTotal")}>
          <div className={cx("cart__footerSubTotalName")}>Subtotal:</div>
          <div className={cx("cart__footerSubTotalNumber")}>$0.00</div>
        </div>
        <div className={cx("cart__footerTotal")}>
          <div className={cx("cart__footerTotalName")}>Total:</div>
          <div className={cx("cart__footerTotalTitle")}>$0.00</div>
        </div>
      </div>
    </div>
  );
}

export default Cart;
